#root {
  margin-left: 0;
  text-align: center;
}
body {
  background-color: #e7e7e7;
  font-family: Arial, Helvetica, sans-serif;
}
html {
  margin-bottom: 100px;
  margin-top: 110px;
  direction: ltr;
}
#cardsContainer {
  margin-left: 0px;
  transition: 500ms;
}

/*--------------------------------- Pc English ---------------------------------*/

.cardPc {
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
  display: inline-block;
  width: 250px;
  height: 400px;
  margin: 20px;
  border: solid 1px #effffa;
  border-radius: 50px 0px 50px 0px;
  -moz-border-radius: 50px 0px 50px 0px;
  -webkit-border-radius: 50px 0px 50px 0px;
  transition: all 500ms;
}
.cardPc:hover {
  -webkit-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  -moz-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  background-blend-mode: multiply; 
  border: solid 1px transparent;
  border-radius: 0px 50px 0px 50px;
  -moz-border-radius: 0px 50px 0px 50px;
  -webkit-border-radius: 0px 50px 0px 50px;
  background-color: gray;
}
.cardPc:active {
  border-radius: 50px 50px 50px 50px;
  -moz-border-radius: 50px 50px 50px 50px;
  -webkit-border-radius: 50px 50px 50px 50px;
  background-color: rgb(71, 71, 71);
}
.kcalDetailsPcEn {
  position: inherit;
  top: 0px;
  left: 0px;
  width: 250px;
  margin: auto;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: left;
  font-size: 17px;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.kcalDetailsPcEn h3 {
  margin-left: 10px;
}
.cardPc:hover .kcalDetailsPcEn {
  visibility: visible;
  top: 175px; 
  color: white; 
}
.titlePcEn {
  position: absolute;
  bottom: 15px;
  left: 10px;
  width: 250px;
  color: #393c45;
  font-size: 25px;
  transition: all 500ms;
}
.cardPc:hover .titlePcEn {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
  left: initial;
}
.cardPc p {
  text-align: right;
  position: absolute;
  right: 0px;
  margin: 0;
  margin-right: 10px;
  display: inline-block;
  color: rgb(162, 244, 255);
}
h4, h5, h6 {margin: 0mm; text-align: left;transition: all 500ms;}
h5 {color: #48d2c1; margin-top: 25px;}
h6 {margin-top: 5px; color: #51575b;}

.cardPc:hover h6, .cardPc:hover h5, .cardPc:hover h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
  display: block;
  margin-top: 5px;
}
#searchInput {
  display: inline-block;
  margin: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 20px;
  border-radius: 20px 0px 20px 0px;
  -moz-border-radius: 20px 0px 20px 0px;
  -webkit-border-radius: 20px 0px 20px 0px;
  border: solid 1px #cecece;
  padding: 10px;
  width: 220px;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  transition: all 500ms;
}
#searchInput:focus {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  background-color: white;
}
#searchInput:hover {
  width: 240px;
  border-radius: 0px 20px 0px 20px;
  -moz-border-radius: 0px 20px 0px 20px;
  -webkit-border-radius: 0px 20px 0px 20px;
  -webkit-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  -moz-box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
  box-shadow: 0px 0px 27px -7px rgba(0,0,0,1);
}
.srchConrainer {
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 1;
  background-color: #e7e7e7;
  width: 100%;
  text-align: center;
  -webkit-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  -moz-box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.10);
  transition: 500ms;
}
#gram {
  font-weight: bold;
  text-align: center;
  width: 45px;
  height: 20px;
  margin-right: 5px;
  border-radius: 5px;
  border: solid 1px #cecece;
  -webkit-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  -moz-box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  box-shadow: inset 0px 0px 44px -24px rgba(0,0,0,0.47);
  display: none;
}
.cardPc:hover #gram {
  display: inline-block;
}
#lang {
  background-color: transparent;
  border: none;
  margin-left: 20px;
  margin-right: 20px;
  font-weight: bold;
  transition: all 500ms;
}
#lang:hover {
  color: #48d2c1;
  font-size: 18px;
  cursor: pointer;
}
#sort {
  background-color: transparent;
  border: none;
  margin-bottom: 5px;
  margin-right: 5px;
  font-weight: bold;
  font-size: 15px;
  transition: all 500ms;
}
#sort:hover {
  color: #48d2c1;
  cursor: pointer;
}
#servingPcEn {
  border-radius: 5px;
  background-color: #e7e7e7;
  color: rgb(77, 77, 77);
  font-weight: bold;
  display: none;
  height: 25px;
  transition: 400ms;
}
.cardPc:hover #servingPcEn {
  display: inline-block;
}

/*--------------------------------- Phone English ---------------------------------*/

.cardPhone {
  position: relative;
  background-repeat: no-repeat;
  background-size: contain;
  background-color: white;
  display: inline-block;
  width: 95%;
  height: 100px;
  margin-bottom: 25px;
  border: solid 1px #effffa;
  border-radius: 50px 0px 50px 0px;
  -moz-border-radius: 50px 0px 50px 0px;
  -webkit-border-radius: 50px 0px 50px 0px;
  transition: all 500ms
}
.cardPhone:hover {
  -webkit-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  -moz-box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  box-shadow: 0px 0px 35px 3px rgba(0,0,0,0.53);
  border: solid 1px transparent;
  border-radius: 30px;
  -moz-border-radius: 30px ;
  -webkit-border-radius: 30px;
  background-color: gray;
  height: 350px;
  background-blend-mode: multiply; 
}
.cardPhone p {
  text-align: right;
  display: inline;
  position: absolute;
  right: 5px;
  margin: 0px;
  color: rgb(162, 244, 255);
}
.titlePhoneEn {
  position: absolute;
  margin: auto;
  bottom: 20px;
  left: 32%;
  text-align: center;
  color: #393c45;
  transition: 500ms;
}
.titlePhoneEn h6, .titlePhoneEn h5, .titlePhoneEn h2 {
  display: block;
  margin: 0;
  text-align: left;
}
.titlePhoneEn h2 {margin-bottom: 10px;}
.cardPhone:hover .titlePhoneEn h6 {margin-bottom: 8px;}
.cardPhone:hover .titlePhoneEn h2 {
  text-align: center;
  margin: 0;
}
.cardPhone:hover .titlePhoneEn {
  width: 100%;
  color: rgb(162, 244, 255);
  text-align: center;
  font-size: 20px;
  left: initial;
  display: block;
  top: 15px;
}
.cardPhone:hover h6, .cardPhone:hover h5, .cardPhone:hover h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 250px;
}
.kcalDetailsPhoneEn {
  position: inherit;
  width: 100%;
  margin: 0px;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: center;
  font-size: 2px;
  visibility: hidden;
  transition: visibility 0ms, top 500ms, color 500ms, font-size 500ms;
}
.kcalDetailsPhoneEn p {margin-right: 15px;}
.kcalDetailsPhoneEn h3 {margin-left: 15px;}
.cardPhone:hover .kcalDetailsPhoneEn {
  visibility: visible;
  top: 120px; 
  color: white; 
  text-align: left;
  font-size: 19px;
  display: inline-block;
}
.cardPhone:hover #gram {
  visibility: visible;
  display: inline-block;
}
.cardPhone:hover span {
  display: none;
}
.cardPhone:hover #servingPcEn {
  display: inline-block;
}

/*--------------------------------- Phone Arabic ---------------------------------*/

.kcalDetailsPhoneAr {
  position: inherit;
  width: 100%;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: center;
  font-size: 2px;
  font-family: 'Amiri', serif;
  display: none;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.kcalDetailsPhoneAr p {
  text-align: left;
  left: 0;
  right: initial;
  margin: 0;
  margin-left: 35px;
}
.kcalDetailsPhoneAr h3 {margin-right: 35px;}
.cardPhone:hover .kcalDetailsPhoneAr {
  top: 125px; 
  color: white; 
  text-align: right;
  font-size: 13px;
  display: inline-block;
}
.titlePhoneAr {
  position: absolute;
  bottom: 10px;
  margin: auto;
  right: 15%;
  text-align: center;
  color: #393c45;
  font-family: 'Amiri', serif;
  direction: rtl;
  transition: all 500ms;
}
.titlePhoneAr h6, .titlePhoneAr h5, .titlePhoneAr h2 {
  display: block;
  margin: auto;
  text-align: right;
}
.cardPhone:hover .titlePhoneAr h2 {right: initial;text-align: center;}
.cardPhone:hover .titlePhoneAr h6 {margin-bottom: 8px;}
.cardPhone:hover .titlePhoneAr {
  color: rgb(162, 244, 255);
  text-align: center;
  font-size: 18px;
  width: 100%;
  right: initial;
  top: 10px;
}
.cardPhone:hover #gram {
  visibility: visible;
  display: inline-block;
}
.cardPhone:hover span {
  display: none;
}

/*--------------------------------- Pc Arabic ---------------------------------*/

.kcalDetailsPcAr {
  position: inherit;
  top: 0px;
  width: 250px;
  margin:auto;
  border: none;
  color: white;
  font-weight: lighter;
  text-align: right;
  font-size: 18px;
  font-family: 'Amiri', serif;
  visibility: hidden;
  transition: visibility 200ms, top 500ms, color 500ms, font-size 500ms;
}
.kcalDetailsPcAr p {
  left: 0px;
  right: initial;
  text-align: left;
  margin-left: 10px;
}
.kcalDetailsPcAr h3 {
  margin-right: 10px;
}
.cardPc:hover > .kcalDetailsPcAr {
  visibility: visible;
  top: 175px; 
  color: white; 
}
.titlePcAr {
  position: absolute;
  bottom: 15px;
  right: 30px;
  width: 250px;
  color: #393c45;
  font-size: 30px;
  font-family: 'Amiri', serif;
  transition: all 500ms;
}
.titlePcAr h6, .titlePcAr h5, .titlePcAr h4 {
  margin: 0;
  text-align: right;
}
.titlePcAr h5 {margin-top: 20px;}
.cardPc:hover .titlePcAr h6, .cardPc:hover .titlePcAr h5, .cardPc:hover .titlePcAr h4 {
  color: rgb(162, 244, 255);
  text-align: center;
  display: block;
}
.cardPc:hover .titlePcAr {
  color: rgb(162, 244, 255);
  text-align: center;
  bottom: 245px;
  right: initial;
}

/*--------------------------------- Pc Kcal Sum ---------------------------------*/

#diaryContainerPc {
  position: fixed;
  top : 150px;
  left: 5px;
  width: 205px;
  margin: 5px;
  z-index: 1;
  overflow-y: scroll;
  overflow: auto;
  max-height: 70%;
  transition: 500ms;
}
#itemsContainerPc {
  position: relative;
  margin-top: 4px;
  border-radius: 5px;
  background-color: white;
  width: 200px;
  height: 50px;
  display: inline-flex;
  transition: 500ms;
}
#itemsContainerPc:hover p, #itemsContainerPc:hover #itemDetailsPc {
  font-size: 15px;
}
#itemsContainerPc:hover #deleteButtonPc {
  transform: rotate(90deg);
}

#itemsContainerPc p {
  text-align: center;
  color: #393c45;
  margin: auto;
  transition: 500ms;
}
.itemNamePc {
  font-weight: bold;
  transition: 500ms;
}
#itemDetailsPc {
  font-size: 14px;
  transition: 500ms;
}
#sumPc {
  margin: auto;
  margin-top: 15px;
  background-color: ivory;
  border-radius: 5px;
  width: 160px;
  height: 30px;
  text-align: center;
  display: none;
  transition: 500ms;
}
#sumPc p {
  margin: auto;
  text-align: center;
  font-weight: bold;
  transition: 500ms;
}
#deleteButtonPc {
  background-color: transparent;
  position: absolute;
  right: 2px;
  top: 2px;
  padding: 0px;
  font-size: 15px;
  border: transparent;
  transition: 500ms;
}
#deleteButtonPc:hover {
  color: #48d2c1;
  cursor: pointer;
}
#addButtonPc {
  background-color: transparent;
  position: absolute;
  right: 2px;
  top: 2px;
  font-size: 20px;
  border: transparent;
  visibility: hidden;
  z-index: 1;
  transition: 500ms;
}
#addButtonPc:hover {
  font-weight: bolder;
  cursor: pointer;
}
.cardPc:hover #addButtonPc {
  right: 12px;
  top: 12px;
  color: white;
  font-size: 30px;
  visibility: visible;
  transform: rotate(90deg);
}

/*--------------------------------- Phone Kcal Sum ---------------------------------*/

#DiaryContainerPhone {
  transition: 500ms;
  width: 100%;
  margin: auto;
  text-align: center;
}
#itemsContainerPhone {
  position: relative;
  margin-top: 4px;
  border-radius: 8px;
  background-color: white;
  width: 75%;
  height: 50px;
  display: inline-flex;
  transition: 500ms;
}
#itemsContainerPhone:hover p, #itemsContainerPhone:hover #itemDetailsPc {
  font-size: 18px;
}
#itemsContainerPhone:hover #deleteButtonPc {
  transform: rotate(90deg);
}
#itemsContainerPhone p {
  font-size: 15px;
  text-align: center;
  color: #393c45;
  margin: auto;
  transition: 500ms;
}
#sumPhone {
  margin: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  background-color: ivory;
  border-radius: 5px;
  width: 160px;
  height: 30px;
  text-align: center;
  display: none;
  transition: 500ms;
}
#sumPhone p {
  margin: auto;
  text-align: center;
  font-weight: bold;
  transition: 500ms;
}
.cardPhone:hover #addButtonPc {
  right: 12px;
  top: 12px;
  color: white;
  font-size: 30px;
  visibility: visible;
  transform: rotate(90deg);
}

/*--------------------------------- Pc Bars ---------------------------------*/

#bars{
  text-align: center;
  font-size: 12px;
}
#bars b {
  display: inline-flex;
  margin: auto;
  font-size: 13px;
  margin-top: 10px;
  text-align: center;
}
.percentNumsPc {
  display: inline-flex;
  text-align: center;
  font-size: 13px;
}
.percentNumsPc b {
  width: 75px;
  margin: auto;
  margin-left: 5px;
}
#barContainer {
  display: inline-flex;
  position: relative;
  background-color: white;
  width: 75px;
  height: 7px;
  margin-left: 5px;
  border-radius: 4px;
}
#barContainer div {
  position: absolute;
  left: 0px;
  margin: 0px;
  border-radius: 4px;
  height: 7px;
  background: rgb(162,255,180);
  background: linear-gradient(90deg, rgba(162,255,180,1) 0%, rgba(0,212,255,1) 100%);
}

/*--------------------------------- Phone Bars English ---------------------------------*/
.percentNumsPhone {
  width: 100%;
  text-align: center;
  font-size: 13px;
}
.percentNumsPhone b {
  display: inline-block;
  width: 75px;
  margin: auto;
  margin-left: 5px;
  margin-top: 20px;
}
/*--------------------------------- Phone Bars Arabic ---------------------------------*/
.percentNumsPhoneArabic {
  width: 100%;
